
const GMTHOUR = 8;

var utils = {
    /**
     * property
     * @param {Object} obj 
     * @param {string} property 
     * @returns {boolean} 
     */
    hasProperty: function (obj, property) {
        return Object.prototype.hasOwnProperty.call(obj, property);
    },

    /**
     * 
     * @param {Object} obj 
     * @returns {boolean} 
     */
    isEmpty: function (obj) {
        if (obj == null) return true;
        if (obj === undefined) return true;
        if (/^\d+$/.test(obj)) return false;
        if (Object.keys(obj).length === 0 && obj.constructor === Object) return true;
        if (typeof obj !== 'object') return true;
        if (Array.isArray(obj) && obj.length === 0) return true;
        if (/^\s*$/.test(obj)) return true;
        return false;
    },

    /**
     * 
     * @param {Object} obj 
     * @returns {boolean} 
     */
    isNotEmpty: function (obj) {
        return !utils.isEmpty(obj);
    },

    /**
     *  blank or null or undefined
     * @param {string} str 
     * @returns {boolean} 
     */
    isBlank: function (str) {
        if (str == null) return true;
        if (str === undefined) return true;
        if (str === 'undefined') return true;
        return (!str || /^\s*$/.test(str));
    },

    /**
     *  blank or null or undefined
     * @param {string} str 
     * @returns {boolean} 
     */
    isNotBlank: function (str) {
        return !utils.isBlank(str);
    },

    /**
     * 
     * @param {string} str1 1
     * @param {string} str2 2
     * @returns {boolean} 
     */
    equals: function (str1, str2) {
        return str1 !== null && str2 !== null && str1.equals(str2);
    },

    /**
     * 
     * @param {string} str1 1
     * @param {string} str2 2
     * @returns {boolean} 
     */
    notEquals: function (str1, str2) {
        return !utils.equals(str1, str2);
    },
};

module.exports = utils;
